@import url('https://fonts.googleapis.com/css?family=Raleway:400,500');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  background-color: #16a085;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  color: #16a085;
}

#quote-box {
  background-color: white;
  width: 25vw;
  height: 100%;
  border-radius: 10px;
  
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
}

#text {
  font-size: 30px;
  font-weight: 400;
  padding: 0px 20px;
}

#author {
  text-align: right;
}

#quoteFooter {
  display: flex;
  justify-content: space-between;
  padding: 0px 0px;
  align-items: center;
}

#socialMedia > a {
  background-color: #16a085;
  margin: 10px;
  padding: 10px 30px;
  border-radius: 5px;
  color:white;
}

#new-quote {
  background-color: #16a085;
  padding: 10px 30px;
  border-radius: 5px;
  color:white;
  border: 0;
}

#developerSign {
  color: white;
  font-size: 15px;
}